import React from "react";
import { Link } from "react-router-dom";
import logoImg from "../assets/img/logo.png";


const Header = () => {
  return (
    <header role="banner" className="header">
      <Link to="/">
        <img 
          src={logoImg} 
          alt="CJENM  " 
          className="header__logo" 
          title=" "
        />
      </Link>
      <nav className="header__nav" aria-label=" ">
        <ul className="header__nav-lists" role="menu">
          <li className="list" role="none">
            <Link 
              to="/about" 
              className="list-link" 
              aria-label="ABOUT US  "
              title="ABOUT US "
            >
              ABOUT US
            </Link>
          </li>
          <li className="list" role="none">
            <Link 
              to="/asset" 
              className="list-link" 
              aria-label="ASSET  "
              title="ASSET  "
            >
              ASSET
            </Link>
          </li>
          <li className="list" role="none">
            <Link 
              to="/news" 
              className="list-link" 
              aria-label="NEWS  "
              title="NEWS  "
            >
              NEWS
            </Link>
          </li>
          <li className="list" role="none">
            <Link 
              to="/contactUs" 
              className="list-link" 
              aria-label="CONTACT US  "
              title="CONTACT US  "
            >
              CONTACT US
            </Link>
          </li>
        </ul>
      </nav>
    </header>
  )
}

export default Header;